import { candidateTableData } from "@/lib/models/candidate";
import { useState, useEffect } from "react";
import { Candidate } from "@/lib/definitions";
import { CandidateCard } from "./candidateCard";

export const ResultCard = ({
  candidateData,
}: {
  candidateData?: Candidate[];
}) => {
  const [currentCandidate, setCurrentCandidate] = useState<Candidate[] | null>(
    null
  );

  useEffect(() => {
    if (candidateData) setCurrentCandidate(candidateData);
  });

  return (
    <div>
      {currentCandidate?.map((candidate) => (
        <div>
          <CandidateCard  candidateData={candidate}/>
          <div className="max-w-md mx-auto p-4 bg-white rounded-lg shadow-md">
            <div className="flex justify-between items-center mb-4">
              <h3 className="font-semibold">Status</h3>
              <div className="flex gap-2">
                {candidate?.isActive === true ? (
                  <div className="flex items-center gap-2">
                    <div className="w-3 h-3 bg-green-500 rounded-full"></div>
                    <p className="text-xs">Active</p>
                  </div>
                ) : (
                  <div className="flex items-center gap-2">
                    <div className="w-3 h-3 bg-red-500 rounded-full"></div>
                    <p className="text-xs">Inactive</p>
                  </div>
                )}
              </div>
            </div>
            <div className="mb-4">
              <h3 className="font-semibold">Skills</h3>
              <div className="flex flex-wrap gap-2">
                {[
                  "React",
                  "Java",
                  "Next.js",
                  "Spring Boot",
                  "Backend",
                  "MongoDB",
                ].map((skill) => (
                  <span
                    key={skill}
                    className="px-3 py-1 bg-gray-200 rounded text-xs"
                  >
                    {skill}
                  </span>
                ))}
              </div>
            </div>
            <div className="flex justify-between items-center">
              <h3 className="font-semibold">Action</h3>
              <div className="flex gap-2">
                <button className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-700">
                  View Resume
                </button>
                <button className="px-4 py-2 bg-gray-500 text-white rounded hover:bg-gray-700">
                  View Profile
                </button>
                <button className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-700">
                  Select
                </button>
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};
