import { useEffect, useState } from "react"
import { Card, CardContent, CardHeader } from "./Card"
import { candidateTableData } from "@/lib/models/candidate"
import { Candidate } from "@/lib/definitions"

export const CandidateCard = ({ candidateData }: { candidateData?:Candidate }) => {

    const[currentCandidate, setCurrentCandidate] = useState<Candidate | null>(null)

    useEffect(() => {
        if(candidateData)
        setCurrentCandidate(candidateData);
    })
    
    return (
        <Card className="p-4 space-y-4">
            <h1 className="font-semibold text-xl">{currentCandidate?.firstName} {currentCandidate?.lastName}</h1>
            <div className="flex items-center gap-6">
                <h3 className="font-semibold text-gray-500">{currentCandidate?.designation}</h3>
                <h3 className="font-semibold text-gray-500">{currentCandidate?.companyName}</h3>
            </div>
            <CardContent className="bg-white p-4">
                <div className="grid md:grid-cols-4 grid-cols-2 gap-4">
                    <div className="space-y-2">
                        <p className="font-semibold">Preferred Job Type</p>
                        <p className="text-gray-500">{currentCandidate?.preferredJobType}</p>
                    </div>
                    <div className="space-y-2">
                        <p className="font-semibold">Gender</p>
                        <p className="text-gray-500">{currentCandidate?.gender}</p>
                    </div>
                    <div className="space-y-2">
                        <p className="font-semibold">Notice Period</p>
                        <p className="text-gray-500">{currentCandidate?.noticePeriod} Days</p>
                    </div>
                    <div className="space-y-2">
                        <p className="font-semibold">Marital status</p>
                        <p className="text-gray-500">{currentCandidate?.maritalStatus}</p>
                    </div>
                    <div className="space-y-2">
                        <p className="font-semibold">Location</p>
                        <p className="text-gray-500">{currentCandidate?.currentLocation.locationDetails}</p>
                    </div>
                    <div className="space-y-2">
                        <p className="font-semibold">Education</p>
                        <p className="text-gray-500">{currentCandidate?.highestEducation}</p>
                    </div>
                    <div className="space-y-2">
                        <p className="font-semibold">Experience</p>
                        <p className="text-gray-500">{currentCandidate?.totalExperience} YRS</p>
                    </div>
                    <div className="space-y-2">
                        <p className="font-semibold">Expected Salary</p>
                        <p className="text-gray-500">{currentCandidate?.currentSalary}</p>
                    </div>
                </div>
            </CardContent>
        </Card>
    )
}